import './WebVisits.scss'
import $ from 'jquery';
import { db } from '../resources/Firebase';
import { Zfill } from '../resources/Helpers';
import { streamer } from '../resources/Config';
import { useEffect, useState } from 'react'

const WebVisits = () => {
	const [counters, setCounters] = useState([]);

    useEffect(() => {
		const arrayDatos = db.collection("users").doc(streamer).collection("buttons").orderBy("timestamp")
        .onSnapshot((arrayCounters) => {
            $('#counters').empty()
            setCounters(boton => boton.concat(arrayCounters.docs))
            arrayCounters.forEach((counter) => {
				fetch(`https://api.countapi.xyz/info/${streamer}/${counter.id}`)
                .then(response => response.json())
                .then(data => {
                    if (data.value) {
                        $(`#${data.key}`).html(Zfill(data.value,10))
                    } else {
                        $(`#${data.key}`).html(Zfill('',10))
                    }
                })
			});
        }, (error) => {
            console.log(error)
        });

        return () => arrayDatos()
    }, [])

    return (
        <>
			<h5 className="text-light titulo-h4 mt-4">Visitas</h5>
            <div id="counters">
				{
					counters.length > 0 ? (
                        counters.map((counter,index) => (
                            <div className="boton-little" key={index}>
								<div className="col d-flex align-items-center">
									<img key={index} src={`./img/${counter.id}.png`} alt={counter.id} className="icon-little"></img>
									<div className="counters" id={counter.id}></div>
								</div>
							</div>
                        ))
                    ) : (null)
                }
			</div>
        </>
    )
}

export default WebVisits;